import Vue from 'vue'
import Vuex from 'vuex'
import axios from './utils/axios'
import apis from './apis'

Vue.use(Vuex)
var loadAction = (commit,payload,mutationName)=>{

  axios.get(payload.api,{pageNo:payload.pageNo,pageSize:payload.pageSize})
  .then((res)=>{
      commit(mutationName,res.data);
  })
}
export default new Vuex.Store({
  state: {
    allArticles: [],
    allCategory: []
  },
  getters:{
    allArticles(state){
      return state.allArticles;
    },
    allCategory(state){
      return state.allCategory;
    }
  },
  mutations: {
    LOADALLARTICLES(state,payload){
      state.allArticles = payload;
    },
    LOADALLCATEGORY(state,payload){
      state.allCategory = payload;
    }
  },
  actions: {
    loadAllArticles({commit},payload={pageNo:1,pageSize:10}){
      payload.api = apis.findArticle;
      loadAction(commit,payload,'LOADALLARTICLES')
    },
    //加载所有的分类
    loadAllCategory({commit},payload={}){
        payload.api = apis.findCategory;
        loadAction(commit,payload,'LOADALLCATEGORY')
    }
  }
})
