import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/Login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/home'
    },
    {
      path: '/login',
      component: login,
      name: 'login'
    },
    {
      path:'/home',
      component:()=>import(/* webpackChunkName: "home"*/ '@/components/Home/Home'),
      name: 'home',
      meta:{
        requireAuth:true
      },
      children: [
        {
          path: 'articleList',
          component:()=>import(/* webpackChunkName: "articleList" */ '@/components/Article/ArticleList'),
          name:'articleList',
        },
        {
          path: 'addNewArticle',
          name:'addNewArticle',
          component: ()=>import(/* webpackChunkName: "addNewArticle" */ '@/components/Article/AddNewArticle'),
        },
        {
          path:'previewArticle',
          name:'previewArticle',
          component: ()=>import(/* webpackChunkName: "previewArticle" */ '@/components/Article/PreviewArticle'),
        },{
          path:'team',
          name:'team',
          component: ()=>import(/* webpackChunkName: "TeamManage" */ '@/components/Team/TeamManage'),
        },
        {
          path: 'category',
          name:'category',
          component: ()=>import(/* webpackChunkName: "Category"*/ "@/components/Category/Category")
        }
      ]
    
    }
  ]
})
